.collection-item{
    width: 22%;
    display: flex;
    flex-direction: column;
    height: 60vh;
    align-items: center;
    .image{
        height: 95%;
        width: 100%;
        background-size: cover;
        background-position: center;
        margin-bottom: 5px;
    }
    .collection-footer{
        width : 100%;
        height: 5%;
        display: flex;
        justify-content: space-between;
        font-size : 18px;
    }
}